// import React from 'react';
import {eventBox} from '../js/Tags';

export default {
    route: "events",
    heading: {
        title: "Recent & Upcoming Events",
        subtitle: "See what Quinn has coming up"
    },
    content: [
        eventBox(
            "Invoke Quartet Readings",
            "Reading of String Quartet No. 2",
            "November 16, 2018 at 7:00pm",
            "Clarice Smith Performaing Arts Center, Gildenhorn Recital Hall",
            "The Invoke Quartet will read and perform new works by UMD student composers. Selections from Quinn's dissertation composition, 13 Episodes for String Quartet will be featured on the concert. Admission is free.",
            "https://farm2.staticflickr.com/1737/40928182370_dfb37a9ae5.jpg"
        ),
        eventBox(
            "World Premier: Dark Nebula",
            "Performacne by UMD UMWO",
            "December 7, 2018 at 8:00pm",
            "Clarice Smith Performaing Arts Center, Dekelbaum Concert Hall",
            "Quinn's Walsum Competition winning work, Dark Nebula, will receive its world premier by the University of Maryland Wind Orchestra under the direction of Michael Votta.",
            "http://www.music.umd.edu/sites/music.umd.edu/files/bands2017v2.png"
        ),

        eventBox(
            "Telegraph Quartet Reading",
            "Reading and Performance by the Telegraph quartet",
            "March 7, 2019 at 7:00pm",
            "Clarice Smith Performaing Arts Center, Gildenhorn Recital Hall",
            "The Telegraph Quartet will read and perform new works by UMD student composers. Selections from Quinn's dissertation composition, 13 Episodes for String Quartet will be featured on the concert. Admission is free.",
            "data:image/jpeg;base64,"
        ),
        
    ]
}